
import CheckList from "./CheckList"

import Panel from "./Panel"

const PhaseCheckList = ({ section }) => {

    return (
        <Panel
            title="Checklist"
            collapsable={true}
            scrollBody={true}
            minHeight="250px"
            maxHeight="590px"
        >
        {section.items && section.items.length > 0
            ? <CheckList steps={section.items} first={true} />
            : <h6> The <b>{section.label}</b> section does not have its own checklist items. Select one of its sub-sections on the left.</h6>
        }
        </Panel>
    )

}

export default PhaseCheckList